import Layout from '../common/Layout'
import styled from 'styled-components';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import firebase from '../firebase';
import { logoutUser } from '../redux/userSlice';
import { useDispatch } from 'react-redux';
import axios from 'axios';

const BtnSet = styled.div`
  margin-top: 20px;
`
function Join() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [Email, setEmail] = useState('');
  const [Pwd1, setPwd1] = useState('');
  const [Pwd2, setPwd2] = useState('');
  const [Name, setName] = useState('');

  const handleJoin = async () => {
    if (!(Name && Email && Pwd1 && Pwd2)) return alert('  ');
    if (Pwd1 !== Pwd2) return alert(' 2  .');
    if (Pwd1.length < 6) return alert('  6  .')

    let createdUser = await firebase.auth().createUserWithEmailAndPassword(Email, Pwd1);
    await createdUser.user.updateProfile({ displayName: Name });

    const item = {
      email: createdUser.user.multiFactor.user.email,
      displayName: createdUser.user.multiFactor.user.displayName,
      uid: createdUser.user.multiFactor.user.uid
    }

    firebase.auth().signOut();
    dispatch(logoutUser());

    axios.post('/api/user/join', item).then(res => {
      if (res.data.success) {

        alert(' .');
        navigate('/login');
      }
      else return alert(' .')
    })
  }



  return (
    <Layout name={'Join'}>
      <input type="email" value={Email} placeholder='  ' onChange={e => setEmail(e.target.value)} />
      <input type="password" value={Pwd1} placeholder=' ' onChange={e => setPwd1(e.target.value)} />
      <input type="password" value={Pwd2} placeholder=' ' onChange={e => setPwd2(e.target.value)} />
      <input type="text" value={Name} placeholder=' ' onChange={e => setName(e.target.value)} />

      <BtnSet>
        <button onClick={() => navigate(-1)}></button>
        <button onClick={handleJoin}></button>
      </BtnSet>
    </Layout>
  );
}

export default Join;
